<template>
  <div class="general">
    <template-test v-bind:url="'testBoyko'" v-bind:identifier="id" ref="template" @addInfo="addInfo"
                   v-bind:header="'Тест Бойко'" v-bind:identifier_field="'test'"
                   @postInfo="postInfo" @newID="newID" class="template-test"></template-test>
    <div class="container" v-if="add">
      <validation-observer ref="observer" v-slot="{ handleSubmit }">
        <b-form @submit.stop.prevent="handleSubmit(save)" @reset="notSave" class="my-form">
          <b-form-group :label="labels[key]" label-align-sm="center"
                        v-if="key!=='attempt'" v-for="(value,key) in labels" :key="key" class="my-form-group">

            <validation-provider rules="required" v-slot="validationContext">
              <b-form-radio-group :options="choices[key]" v-model="items[key]" :value="items[key]"
                                  :state="getValidationState(validationContext)" class="my-radio"></b-form-radio-group>
            </validation-provider>

          </b-form-group>
          <button class="btn btn-default" type="reset">Отмена</button>
          <button class="btn btn-default" type="submit">Добавить</button>
        </b-form>
      </validation-observer>
    </div>
    <div v-if="templateBool">
      <MainFooter v-if="$refs.template.labels || choices" class="noprint"></MainFooter>
    </div>
  </div>
</template>

<script>
import templateTest from "./templateTest";
import {ValidationObserver, ValidationProvider} from "vee-validate/dist/vee-validate.full";
import MainFooter from "../footers/MainFooter";

export default {
  name: "TestBoyko",
  components: {
    templateTest,
    ValidationProvider,
    ValidationObserver,
    MainFooter,
  },
  data() {
    return {
      add: false,
      labels: '',
      choices: '',
      items: '',
      id: '',
      attempt: '',
      templateBool: false
    }
  },
  created() {
    if (this.$route.params.id !== undefined && this.$route.params.attempt) {
      sessionStorage.setItem('id_test', this.$route.params.id);
      sessionStorage.setItem('test_attempt', this.$route.params.attempt);
    }
    this.id = sessionStorage.getItem('id_test');
    this.attempt = sessionStorage.getItem('test_attempt');
  },
  mounted: function () {
    this.templateBool = true
  },
  methods: {
    addInfo() {
      this.add = true;
      $.ajax({
        url: this.$store.state.baseUrl + "api/fields/",
        type: "GET",
        data: {model: 'TestBoyko'},
        success: (response) => {
          this.labels = response.data.labels;
          this.choices = response.data.choices;
          if (this.$refs.template.edit !== '')
            this.items = this.$refs.template.edit;
          else
            this.items = response.data.items;
        },
        error: (response) => {
          if (response.status === 401) this.logOut();
          else alert("Не удалось получить данные с сервера.\nПовторите попытку позже.")
        }
      })
    },
    save() {
      this.items['client'] = parseInt(sessionStorage.getItem('id'));
      this.items['attempt'] = parseInt(sessionStorage.getItem('test_attempt'));
      if (this.$refs.template.edit !== '')
        this.$refs.template.putRequest(this.items);
      else this.$refs.template.postRequest(this.items);
    },
    postInfo() {
      this.add = false;
    },
    notSave() {
      this.$refs.template.labels = this.labels;
      this.add = false;
      this.$refs.template.isHide = false;
    },
    getValidationState({dirty, validated, valid = null}) {
      return dirty || validated ? valid : null;
    },
    newID(id) {
      sessionStorage.setItem('id_test', id);
      this.id = id;
    }
  }
}
</script>

<style scoped>
@import "../../assets/css/edit-style.css";

.my-form-group {
  background: #EDE5C9;
  margin-right: 0;
}

.my-radio {
  text-align: left;
  font-size: 14px;
  margin-left: 5%;
  padding: 10px;
}

/deep/ .custom-control {
  padding-top: 10px;
  display: block;
}

/deep/ .text-sm-center {
  margin-top: 2%;
  font-weight: bold;
  background: #EDE5C9;
}
</style>



